@import "src/n1-main/m1-ui/common/styles/sass/variables";
@import "src/n1-main/m1-ui/common/styles/sass/mixins";


.tableData {
  @include text($fontFamilyText, 13px, 500, 16px, black, left);
  display: grid;
  grid-template-columns: 20% 55% 15% 10%;
  background: #ECECF9;
  align-items: center;
  justify-content: start;
  padding: 10px 0;

  div {
    margin-left: 24px;
  }

  .buttonsContainer {
    text-align: left;

    .deleteButton {
      background: #F1453D;
      border-radius: 5px;
      cursor: pointer;
      margin-right: 15px;

      span {
        @include primaryButtonText(5px);
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        color: #FFFFFF;
      }
    }

    .editButton{
      background: #D7D8EF;
      border-radius: 5px;
      cursor: pointer;
      margin-right: 15px;

      span {
        @include primaryButtonText(5px);
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        color: $elementsColor;
      }
    }

    .deleteButton:disabled{
      opacity: 0.3;
      cursor: not-allowed;
    }
  }
}

.gridChangeClass{
  grid-template-columns: 25% 25% 15% 15% 20%;
}

.tableData:nth-child(even) {
  background: #FFFFFF;
}

.tableData:nth-child(odd) {
  background: #F8F7FD;
}
